class NavbarCustom extends HTMLElement {
  constructor() {
    super();

    this.render();
  }

  render() {
    this.innerHTML = `
    <nav class="nav-bar" id="#navigationDrawer">

    <div class="logo"> 
      <img src="./images/icon/logo.svg" class="logo-img" alt="logoAplikasi">
      <h1>Rest-aurant</h1>
    </div>

  <button id="hamburger" href="#" class="togle" tabindex="0">☰</button>
    <ul>
      <li><a href="/" id="home-link">Home</a></li>
      <li><a href="#/like" id="favorite-link">Favorite</a></li>
      <li><a href="https://www.linkedin.com/in/kenni-hasim-749712291" id="about-us-link">About Us</a></li>
    </ul>
  
  </nav>
    `;
  }
}

customElements.define('navbar-custom', NavbarCustom);
